.img__container { 
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.koolduct__img--container {
	position: relative;
	margin: 10px;
	width: 250px;
	height: 250px;
	overflow: hidden;
}


.koolduct__img {
	margin: 10px;
	object-fit: cover;
  	width:250px;
  	height:250px;
}

.white-popup {
  position: relative;
  background: #FFF;
  padding: 20px;
  width: auto;
  max-width: 500px;
  margin: 20px auto;
}

.inner-popup {
	display: inline;
}

.koolduct__logo {
	background-color: #FFF;
	padding: 5px;
}


